import { Component } from '@wordpress/element'
import { PanelBody, SelectControl, RangeControl, ToggleControl, } from '@wordpress/components'
import { InspectorControls, } from '@wordpress/block-editor'

export default class Inspector extends Component {
  constructor (props) {
    super(props)
  }
  render () {
    const {
      attributes: {
        carouselVariantDark,
        carouselInterval,
        carouselCaption,
        carouselIndicators,
        carouselControls,
        carouselFade,
        carouselWrap,
        carouselUrl,
        carouselPause
      },
      setAttributes,
    } = this.props

    return (
      <InspectorControls>
        <PanelBody title="" initialOpen={true}>
          <RangeControl
            label="  ()"
            value={carouselInterval}
            renderTooltipContent={value => `${value} .`}
            onChange={(value) =>
              setAttributes({ carouselInterval: value })
            }
            min={1}
            max={20}
          />
          <ToggleControl
            label=" "
            help="  ,     ,   ."
            checked={carouselVariantDark}
            onChange={(value) =>
              setAttributes({ carouselVariantDark: value })
            }
          />
          <ToggleControl
            label="  "
            help="    ."
            checked={carouselCaption}
            onChange={(value) =>
              setAttributes({ carouselCaption: value })
            }
          />
          <ToggleControl
            label=""
            help="    ."
            checked={carouselIndicators}
            onChange={(value) =>
              setAttributes({ carouselIndicators: value })
            }
          />
          <ToggleControl
            label=""
            help="  ''  ''      ."
            checked={carouselControls}
            onChange={(value) =>
              setAttributes({ carouselControls: value })
            }
          />
          <ToggleControl
            label=" "
            help="         ."
            checked={carouselFade}
            onChange={(value) =>
              setAttributes({ carouselFade: value })
            }
          />
          <ToggleControl
            label=" "
            help="        ."
            checked={carouselWrap}
            onChange={(value) =>
              setAttributes({ carouselWrap: value })
            }
          />
          <ToggleControl
            label=" "
            checked={carouselUrl}
            onChange={(value) =>
              setAttributes({ carouselUrl: value })
            }
          />
          <ToggleControl
            label=" "
            help="       "
            checked={carouselPause}
            onChange={(value) =>
              setAttributes({ carouselPause: value })
            }
          />
        </PanelBody>
      </InspectorControls>
    )
  }
}
